body {
  color: black;
}

.sidebar {
  min-width: 250px;
  grid-area: sidebar;
  justify-self: stretch;
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  z-index: 1;
  background-color: #B6CADA;
  box-shadow: grey 0 0 5px;
}

.sidebar-item {
  padding: 1em 1em;
  text-decoration: none;
  color: black;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.sidebar-item.selected {
    background-color: #42A9CA;
    color: white;
}

.sidebar-item.selected::after {
  content: "";
  border-left: 1em solid #42A9CA;
  border-top: 27px solid transparent;
  border-bottom: 27px solid transparent;
  margin: -2em;
}

.sidebar-item.sub {
  padding: 1em 1em 1em 2em;
  width: calc(100% - 3em);
}

.sidebar-item:hover {
  background-color: #eee;
}

.sidebar-item:hover::after {
  border-left: 1em solid #eee;
}

.content-wrapper {
  grid-area: center;
  justify-self: center;
  width: calc(100% - 40px);
  min-height: calc(100vh - 236px);
  margin: 0;
}

.guide-body {
  display: grid;
  grid-template-columns: auto minmax(0px, 960px) auto;
  grid-template-rows: auto;
  grid-template-areas: "sidebar center empty";
}